<template>
  <div class="complete">
    <div class="complete__container">
      <h2 class="complete__title">{{ congra.substring(0, textleng) }}</h2>
      <img :class="textleng >= leng ? ' show' : ''" src="@/assets/pics/1.jpg" alt="" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Complete',
  data() {
    return {
      textleng: 0,
      leng: 0,
      congra: 'CONGRATULATIONS! This gift is for you'
    }
  },
  mounted() {
    this.leng = this.congra.length
    for (let i = 0; i < this.leng; i++) {
      setTimeout(() => {
        this.textleng++
      }, i * 200)
    }
    console.log(this.answersGetter.join(''))
  },
  computed: {
    ...mapGetters(['answersGetter'])
  },
  methods: {}
}
</script>
